<template>
  <auth-layout>

    <form class='auth-form' @submit.prevent='report()'>

      <h2>Техпідтримка</h2>
      <div class='modal-text'>Напишіть нам у месенджері:</div>
      <div class='display: flex'>
        <a class="icon-social" href='https://telegram.me/sky_wing' target="_blank" rel="noopener noreferrer" ><img src='@/assets/icon-telegram.png'></a>
        <a class="icon-social" href='viber://chat?number=%2B380665299228' target="_blank" rel="noopener noreferrer" ><img src='@/assets/icon-viber.png'></a>
        <a class="icon-social" href='https://wa.me/380665299228' target="_blank" rel="noopener noreferrer" ><img src='@/assets/icon-wa.png'></a>
      </div>
      <div class="modal-separator">або</div>
      <div class="modal-text">Опишіть проблему тут, і ми відповімо вам електронною поштою:</div>
      <input-email v-model='reportData.email' ref='email'/>
      <input-textarea v-model='reportData.text' placeholder="Питання чи опис проблеми" ref='inputText'/>

      <button type='submit' class='button-plain auth-button'>Надіслати</button>
      <small><router-link to='/auth'>Назад до авторизації</router-link></small>

    </form>

  </auth-layout>
</template>

<script>
import controller from '@/api/controller'
import inputEmail from '@/components/inputs/input-email.vue'
import inputTextarea from '@/components/inputs/input-textarea.vue'
import authLayout from '@/layouts/auth-layout.vue'
export default {
components: { authLayout, inputEmail, inputTextarea },
  data() { return {
    reportData: {
      email: '',
      text: '',
    },
  }},
  computed: {
    inputs() { return Object.keys(this.$refs).map(e => this.$refs[e]) },
  },
  methods: {
    async report() {
      this.inputs.map(e => e.v$.$touch())
      if (!this.inputs.some(e => e.v$.$error)) {
        await controller.report('Нове звернення на skywing.online', this.reportData)
        this.$emit("update:visible", false)
      }
    }
  }
}
</script>

<style lang='scss' scoped>
// * {border: 1px solid red}
.auth-form {
  width: min(22.5em, 80%);
}
.icon-social {
  width: 3em;
  height: 3em;
  margin: 10px;
  &:hover { opacity: .75 }
  &:active { opacity: .75 }
  img {
    width: 100%;
    height: 100%;
  }
}
.modal-separator {
  display: flex;
  gap: 10px;
  margin: 1.5em 0;
  font-size: .9em;
  color: #0003;
  &::before, &::after{
    content: "";
    flex: 1 1;
    border-bottom: 1px solid;
    margin: auto;
  }
}
</style>